@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Unna&display=swap');

  .hero {
    width: 100%;
    height: 50vh;
    background: rgba(13, 14, 13, 0.5);  }
  
  .hero:before {
    content: '';
    position: absolute;
    background: url('../../assets/hero-bg.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }


.hero .hero-container {

    align-items: left;
    text-align: left;
    width: 100%;
}

.hero .content {
  width: 55%;
  top: 20%;
  left: 5%;
  position: absolute;
  padding: 1rem;
}


.hero h1 {
    font-size: 3rem;
    color: rgb(255, 255, 255);
    font-family: 'Unna', serif;    
    padding: 1rem 0;
    line-height: 2.5rem;
    letter-spacing: 2px;
}

.hero p {
    font-size: .8rem;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
}



/* MEDIA QUERIES */

@media screen and (max-width:940px) {

  .hero .content {
    width: 100%;
    top: 20%;
    left: 0%;
  }

    .hero h1 {
        font-size: 2rem;
        text-align: center;
    }


}
